import React from 'react';

import { MyTextField } from '../CustomInputs';

function NotesFormInputs() {
  return (
    <React.Fragment>
      <MyTextField
        margin="normal"
        name="note"
        label=""
        autoComplete="off"
        multiline
        fullWidth
      />
    </React.Fragment>
  );
}

export default NotesFormInputs;
