import React from 'react';

import { Grid, FormControl, InputLabel, MenuItem } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { MyTextField, MySelect } from '../CustomInputs';

const menuProps = {
  anchorOrigin: {
    vertical: 'bottom',
    horizontal: 'left',
  },
  transformOrigin: {
    vertical: 'top',
    horizontal: 'left',
  },
  getContentAnchorEl: null,
};

const days = {
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
};

const iconComponent = (props) => <ExpandMoreIcon {...props} />;

function AddScheduleInputs() {
  return (
    <React.Fragment>
      <Grid container spacing={4}>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth>
            <InputLabel id="pupil-form__select-label"> </InputLabel>
            <MySelect
              labelId="pupil-form__select-label"
              name="day"
              MenuProps={menuProps}
              IconComponent={iconComponent}>
              {Object.keys(days).map((key) => (
                <MenuItem key={key} value={key}>
                  {days[key]}
                </MenuItem>
              ))}
            </MySelect>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <MyTextField
            margin="none"
            name="time"
            type="time"
            label=" "
            inputProps={{
              step: 300, // 5 min
            }}
            fullWidth
          />
        </Grid>
      </Grid>
      <MyTextField
        margin="normal"
        name="price"
        type="number"
        label=" "
        autoComplete="off"
        fullWidth
      />
      <MyTextField margin="normal" name="subject" label="" autoComplete="off" fullWidth />
    </React.Fragment>
  );
}

export default AddScheduleInputs;
