import * as yup from 'yup';

const validationSchema = yup.object().shape({
  firstName: yup
    .string(' ')
    .max(100, '     100 ')
    .required('  '),
  lastName: yup
    .string(' ')
    .max(100, '     100 ')
    .required('  '),
  email: yup
    .string('   ')
    .email('   ')
    .required('   . '),
  password: yup
    .string(' ')
    .min(6, '   6 ')
    .max(30, '   30 ')
    .required(' '),
  recaptcha: yup
    .mixed()
    .notOneOf([''], '  Recaptcha')
    .required('  Recaptcha'),
});

export default validationSchema;
