import * as yup from 'yup';

const MAX_LESSON_PAY = 999999;

const validationSchema = yup.object().shape({
  theme: yup
    .string('  ')
    .max(200, '  ,  ')
    .required('   '),
  pupil: yup.mixed().typeError('   ').required(' '),
  note: yup.string('').max(500, '     500 '),
  subject: yup
    .string(' ')
    .max(100, '  ,  ')
    .typeError('   ')
    .required('  '),
  date: yup.string('  ').required('  '),
  time: yup
    .string('  ')
    .matches(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, '    : "17:00"'),
  price: yup
    .number('  ')
    .typeError('  ')
    .min(0, 'C   ')
    .max(MAX_LESSON_PAY, '        ')
    .required('  '),
});

export default validationSchema;
