import * as yup from 'yup';

const MAX_LESSON_PAY = 999999;

const validationSchema = [
  yup.object().shape({
    name: yup
      .string('  ')
      .typeError('   ')
      .max(100, '  100 ?')
      .required('   '),
    grade: yup // number?
      .string('    ?')
      .typeError('   ')
      .matches(/^([1-9]?|1[0-9])$/, '   1  11')
      .max(2, '  ,   ')
      .required(' ,    '),
    address: yup
      .string(' ')
      .max(200, '   ')
      .typeError('   '),

    parents: yup.array().of(
      yup.object().shape({
        contact: yup
          .string('  ')
          .matches(/^[[0-9]+$/, '  ')
          .typeError('   ')
          .max(20, '   '),

        person: yup
          .string('   ')
          .max(100, 'C   ')
          .typeError('     '),
      }),
    ),
  }),

  yup.object().shape({
    schedules: yup.array().of(
      yup.object().shape({
        day: yup
          .mixed()
          .oneOf(['1', '2', '3', '4', '5', '6', '7'])
          .typeError('   ')
          .required('  '),
        time: yup
          .string('  ')
          .matches(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, '    : "17:00"')
          .max(5, '       ')
          .required('  '),
        price: yup
          .number('  ')
          .typeError('  ')
          .min(0, '   ')
          .max(MAX_LESSON_PAY, '        ')
          .required('  '),
        subject: yup
          .string()
          .max(200, '  ,  ')
          .typeError('   ')
          .required('  '),
      }),
    ),
  }),
];

export default validationSchema;
